@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
body{
    font-weight: 300;
    font-family: 'Open Sans', sans-serif;
    background-color: #FAF8F0;
    margin-top:75px;
    padding: 0;
}

.add-margin-left{
    margin-left: 250px;
}

h1,h2{
    font-weight: 400;
    color: #043353;
}

.light-weight{
    font-weight: 300;
}

.regular-weight{
    font-weight: 400;
}

.bold-weight{
    font-weight: 700;
}

/* FORMS */
.form-style{
    color:#043353;
}

.form-title{
    padding-top: 25px;
    color:#043353;
}

.form-control{
    color:#02263F;
    background-color: #F8F8F8;
}
.form-style label{
    color:#043353;
    font-weight: 400;
}


hr{
    border-top: 1px solid #E44652;
}

.form-button{
    color:#FAF8F0;
    background-color: #E44652;
    border: none;
    padding: 8px 20px;
    border-radius: 5px;
    font-weight: 700;
}

.form-button:hover{
    color:#FAF8F0;
    background-color: #E44652;
}

.form-button:active{
    color:#FAF8F0;
    background-color: #E44652;
}

.link-form{
    color:#E44652;
    text-decoration: none;
}

.link-form:hover{
    color:#E44652;
    text-decoration: none;
}

.shadow-up{
   box-shadow: 0px -2px 8px 0px rgba(0,0,0,0.75); 
}

.shadow-down{
   box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.75); 
}

.shadow-right{
   box-shadow: 2px 0px 8px 0px rgba(0,0,0,0.75); 
}

.marker{
    width:20px;
    height:20px;
    background-color: #FAF8F0;
    border: 3px solid #E44652;
    border-radius: 50%;
}
.top-bar{
    background-color: #02263F;
    color:#FAF8F0;
    z-index: 2000;
    height: 75px;
}
.top-bar img{
    width:190px;
}

.top-bar .logo{
    width: 215px;
    text-align: center;
    padding-left: 0px;
    margin-left: 0px;
    font-size: 1.7em;
}

.top-bar a{
    color:#FAF8F0;
    text-decoration: none;
}

.login-button{
    font-weight: 300;
    background-color: #02263F;
    border:none;
    color:#FAF8F0;
}

.top-bar form{
    display: flex;
    align-items: center;
    margin-left: 50px;
}


.top-bar input{
    padding: 8px 20px;
    margin-left: 210px;
    border: none;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    width: 500px;
    box-shadow: inset 0px 0px 19px -5px rgba(0,0,0,0.8);
}

.top-bar .search-button{
    color:#FAF8F0;
    background-color: #E44652;
    border: none;
    padding: 8px 20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;    
    font-weight: 700;
}

.top-bar nav{
    width: 100%;
    display:flex;
    justify-content: space-between;
}
.landing-carousel img{
    height: 800px;
    object-fit: cover;
}


.search-card{
    border: 2px solid #043353;
    border-radius: 8px;
    margin-top: 20px;
    color: #043353;
    padding:20px 15px 0px;
    
    background-color: #FAF8F0;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);
    display: flex;
    justify-content: space-between;
}

.search-card:hover{
    text-decoration: none;
    color: #043353;
    box-shadow: none;

}

.search-card figure{
    width:20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0px 15px;
}

.search-card h2{
    color: #043353;
    font-size: 1em;
    font-weight: 700;
}

.search-card h1{
    color: #043353;
    font-size: 1.2em;
    font-weight: 400;
}

.search-card img{
    width: 100px;
    border-radius: 5px;
    margin-bottom: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}

.search-card div{
    padding:0px 15px 15px;
    width:70%;
    font-size: 1.3em;
    text-align: start;
}

.search-card .avatar{
    border-radius: 50%;
    border: 2px solid #043353;
}


.side-bar{
    width:250px;
    height: 100vh;
    background-color: #043353;
    position: fixed;
    z-index: 1000;
    left: 0px;
    color: #FAF8F0;
    padding-top: 25px ;
    padding-right: 30px;
    font-size: 1.2em;
    font-weight: 400;
}

.side-bar img{
    width:70px;
    height: 70px;
    object-fit: cover;
    border-radius: 50%;
    border: 1.5px solid #FAF8F0;
}

.side-link{
    color: #FAF8F0;
    text-decoration: none;
}

.side-bar li{
    list-style: none;
    text-align: end;
}

.side-bar hr{
    border-top: 1px solid #14496D;
}

.side-link:hover{
    color: #FAF8F0;
    text-decoration: none;
}
.meeting-details section{
    padding: 50px 70px;
    color:#02263F;
}

.meeting-details h1{
    color:#043353;
}

.meeting-details h2{
    color:#043353;
    font-weight: 700;
    font-size: 1.5em;
}

.meeting-details h3{
    font-weight: 700;
    color:#E44652;
    text-transform: uppercase;
    font-size: 1.05em;
    text-align: center;
}

.meeting-details .username{
    color:#043353;
    margin-top: 10px;
}

.meeting-details article{
    padding-left: 20px;
    padding-right: 20px;
}

.meeting-details .poster{
    margin-bottom: 50px;
}

.meeting-details .poster img{
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
    width: 75%;
}

.meeting-details .separation{
    display: flex;
    justify-content: center;
}

.meeting-details .separation div{
    width: 0%;
    border: 1px solid #E44652;
    height: 100%;
    padding: 0;
}

.meeting-details hr{
    width: 80%;
    margin:20px auto;
}

.meeting-details .avatar{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meeting-details .avatar img{
    border-radius: 50%;
    width: 60%;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
    border: 2px solid #043353;
}

.meeting-details .participants{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.meeting-details .participants div{
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.meeting-details h4{
    font-size: .9em;
}

.meeting-details .participants img{
    width: 70%;
    border-radius: 50%;
    margin: 10px 0 ;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
    border: 2px solid #043353;
}


.following{
    padding: 30px 50px 100px;
}

.following h1{
    text-align: center;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 1.9em;
}
.following h2{
    margin-bottom: 15px;
    font-size: 1.4em;
    text-transform: uppercase;
    display: inline;
}

.following .card{
    display: flex;
    flex-direction: column;
    align-items: center;
    color:#043353;
    padding: 20px;
    margin:10px 10px;
    min-height: 100%;
    width:100%;
    border: 2px solid #043353;
    background-color: #FAF8F0;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    text-align: center;
}

.following  img{
    width: 90%;
    border-radius: 7px;
    margin: 20px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
}

.following .card>p{
    width: 90%;
    font-size: 1.3em;
    font-weight: 400;    
}

.following .msg{
    margin-top: 50px;
    text-align: center;
    color:#E44652;
    
}

.following .msg>p{
    display: inline;
    background-color: #FAF8F0;
    border: 2px solid #043353;
    padding: 18px 25px;
    border-radius: 10px;
    font-size: 1.3em;
    font-weight: 700;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
}

.following-button{
    border: none;
    color: #FAF8F0;
    background-color: #E44652;
    font-size: 1.5em;
    font-weight: 700;
    padding: 0px 12px;
    margin-left: 10px;
    border-radius: 50%;
}


.creating-meeting{
    color: #043353;
}

.creating-meeting label{
    font-weight: 400;
}

.creating-meeting input{
        color: #043353;
        border: 1px solid #E44652;
}
.form-control{
    color: #043353;
    background-color:#F8F8F8;
    border: 1px solid #E44652;
}
.creating-meeting .form-date{
    border-radius: .25rem;
    padding: .375rem .75rem;
    background-color:#F8F8F8;
}

.search-results{
    margin-top: 3px;
    max-width: 500px;
    min-width: 500px;
    max-height: 200px;
    overflow-y: scroll;
    position: absolute;
    z-index: 1000;
    background-color: #F8F8F8;
    color:#E44652;
    padding-left: 0px;
    font-weight: 400;
    border-radius: .25rem;
    box-shadow: inset 0px 0px 20px -10px rgba(0,0,0,0.8);
}

.search-results li{
    list-style: none;
    margin-bottom: 10px;
    padding-left: 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: .25rem;
    min-height: 60px;
}

.search-results li:hover{
    
    background-color: #E44652;
    color:#FAF8F0;
}

.search-results li>p{
    padding: 0;
    margin: 0;
    margin-left: 20px;
}

.miniature{
    max-height: 70px;
    margin-left: 20px;
}
.miniature:hover{
    max-height: 140px;
}
